.scroll-up-btn {
  background-color: var(--white);
  opacity: 0;
  visibility: hidden;
  transition: background-color var(--delay) var(--trnz), opacity var(--delay) var(--trnz), visibility var(--delay) var(--trnz);
  cursor: pointer;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  padding: 5px;
  display: flex;
  position: fixed;
  bottom: 20px;
  right: 15px;
}

