.scroll-up-btn:after {
  z-index: -1;
  content: "";
  background-color: var(--olive-accented);
  transform-origin: top;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  transition: transform .7s;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleY(0);
}

.scroll-up-btn:active:after {
  transform-origin: bottom;
  transform: scaleX(1);
}

@media only screen and (min-width: 1440px) {
